import React from "react"
import * as THREE from "three"
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls' 
import { STLLoader } from 'three/examples/jsm/loaders/STLLoader' 

class STLModel extends React.Component {

    constructor() {
        super()

        // this.state = {
        //     stlGeometry: "no geometry",
        // }

        // this.stl = this.props.stl
    }

    componentDidMount() {

        console.log(this.props.stl)
        
        // let scene = new THREE.Scene()
        var scene = new THREE.Scene();
        
        scene.add(new THREE.HemisphereLight(0xffffff, 1.5)) 

        var elem = document.getElementById("stl-model")
        
        // let camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000)
        var camera = new THREE.PerspectiveCamera(75, elem.clientWidth / elem.clientHeight, 0.1, 1000)
        
        // let renderer = new THREE.WebGLRenderer()
        var renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
        renderer.setSize(elem.clientWidth, elem.clientHeight);
        
        // renderer.setSize(window.innerWidth / 2, window.innerHeight / 2)
        
        elem.appendChild(renderer.domElement)
        // document.body.appendChild(renderer.domElement)

        var controls = new OrbitControls(camera, renderer.domElement)
        
        controls.enableDamping = true
        controls.dampingFactor = 0.1
        controls.rotateSpeed = 0.2
        controls.autoRotate = true
        controls.autoRotateSpeed = .75
        controls.enableZoom = false
        controls.enablePan = false

        var loader = new STLLoader()

        // console.log(this.state.stlGeometry)

        loader.load(this.props.stl, function (geometry) {
            
            console.log("Inside Loaded Function")

            const cubeGeometry = new THREE.BoxGeometry()
            // const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 })
            const cube = new THREE.Mesh(cubeGeometry, material)
            // scene.add(cube)

            var material = new THREE.MeshPhongMaterial({
                color: 0xff5533,
                specular: 100,
                shininess: 100
            })

            const stl = new THREE.Mesh(geometry, material)

            var middle = new THREE.Vector3()
            geometry.computeBoundingBox()
            geometry.boundingBox.getCenter(middle)
            
            stl.geometry.applyMatrix(new THREE.Matrix4().makeTranslation(-middle.x, -middle.y, -middle.z))




            scene.add(stl)

            // this.setState({stlGeometry: geometry})

            // stlGeometry = geometry


            // console.log(this.state.stlGeometry)

            var largestDimension = Math.max(geometry.boundingBox.max.x,
                geometry.boundingBox.max.y,
                geometry.boundingBox.max.z)
            camera.position.z = largestDimension * 2.5
            
        })

        console.log("Outside Loaded Function")

        // console.log(this.state.stlGeometry)

        // const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 })

        


        // var stl = new THREE.Mesh(this.state.stlGeometry, material)

        // console.log(stl)

        // scene.add(stl)

        // camera.position.z = 5
        // camera.position.z = largestDimension * 1.5
        
        var animate = function () {
            requestAnimationFrame(animate)
            // cube.rotation.x += 0.01
            // cube.rotation.y += 0.01
            controls.update()
            renderer.render(scene, camera)
        }

        animate()

    }

    render() {
        return (
            <div id={"stl-model"} style={{
                height: "500px",
                width: "500px",
            }}></div>
        )
    }
}

export default STLModel